.project-bg-wrap {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000000;
}

.project-bg {
  position: absolute;
  width: 100%;
  height: 100vh;
  background-color: rgba(0,0,0,0.4);
}

.project-wrapper {
    position: relative;
    z-index: 990;
    width: 80%;
    height: 600px;
    padding: 20px 20px 45px 20px;
    background: radial-gradient(rgba(85, 164, 221, 0.3),#47b8e8);
    border: 1px solid #fff;
    animation: appear 1s ease-out;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: start;
  }
  
  .project-box {
    width: 100%;
    height: 100%;
    background-color: rgb(230, 230, 230);
    display: flex;
    border-radius: 4px;
    padding: 10px;
    border: 2px solid rgb(215, 215, 215);
  }
  
  @media (max-width: 760px) {
    .project-box {
      flex-direction: column;
    }
    .project-content {
      width: 100% !important;
    }
    .project-img {
      width: 100% !important;
      height: 200px !important;
    }
    .project-img > img {
      width: auto !important;
    }
  }
  
  .project-img {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 30%;
    height: 100%;
    border-radius: 4px;
    padding: 30px;
    margin-right: 7px;
    border-right: 1px solid rgb(217, 217, 217);
    box-shadow: 5px 5px 5px 0px #d8d8d8;
  }
  
  .name {
    width: 130px;
    margin-top: 10px;
    border: 1px solid #333;
    border-radius: 5px;
    background-color: #fff;
    color: #fff;
  }
  
  .name > span {
    text-align: center;
    display: block;
    width: 100%;
    border: 1px solid #fff;
    border-radius: 5px;
    margin: 1px;
    padding: 4px;
    background: radial-gradient(rgb(121,121,121),rgb(133, 133, 133));
  }
  
  .project-img > img {
    width: 100%;
    max-width: 150px;
    height: auto;
    object-fit: fill;
  }
  
  .project-content {
    width: 70%;
    height: 100%;
    overflow-y: auto;
    border-radius: 8px;
    background-color: #fff;
    border: 1px solid rgb(215, 215, 215);
    padding: 30px;
  }
  
  .click-me__txt {
    position: absolute;
    white-space: nowrap;
    bottom: 100%;
    font-size: 20px;
    color: #fff;
    text-shadow: -2px 0px orange, 0px 2px orange,
    1px 0px orange, 0px -2px orange;
    margin-bottom: 10px;
    animation: blink 1s step-end infinite;
  }
  
  @keyframes blink {
    50% {
      opacity: 0;
    }
  }
  
  .project-content__title {
    text-align: left;
    font-size: 20px;
    margin-top: 15px;
    color: #fff;
    text-shadow: -2px 0px rgb(255, 0, 0), 0px 2px rgb(255, 0, 0),
      1px 0px rgb(255, 0, 0), 0px -2px rgb(255, 0, 0);
  }

  .company-logo {
    width: 50px;
    height: 50px;
  }
  
  .project-content__ul {
    list-style: none;
    text-align: left;
    padding: 0;
  }
  
  .project-content__li {
    display: flex;
    align-items: center;
    font-size: 18px;
    /* cursor: pointer; */
    color: rgb(90, 65, 138);
    width: fit-content;
    margin-bottom: 10px;
  }
  
  .project-content__li:hover {
    text-decoration: underline dotted;
  }
  
  .arrow {
    width: 20px;
    height: 20px;
  }
  
  .prev-btn {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    /* cursor: pointer; */
    font-size: 18px;
    bottom: 8px;
    height: 30px;
    width: 80px;
    background: radial-gradient(rgb(208, 255, 19),#95d84d);
    color: #fff;
    border-radius: 8px;
    border: 1px solid rgb(94, 153, 127);
    text-shadow: -2px 0px rgb(94, 153, 127), 0px 2px rgb(94, 153, 127),
      1px 0px rgb(94, 153, 127), 0px -2px rgb(94, 153, 127);
  }
  
  .prev-btn:hover {
    background: radial-gradient(rgb(208, 255, 19),#7daa4e);
  }
  
  .prev-btn > img {
    margin-right: 8px;
    width: 10px;
    height: 10px;
    filter: opacity(0.8) drop-shadow(0 0 0 rgb(94, 153, 127));
  }

  .intro-text {
    line-height: 30px;
    color: #7a7a7a;
  }

  .pixel-icon {
    width: 50px;
    height: auto;
    margin-right: 10px;
  }

  .me-info {
    font-size: 18px;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: center;
    margin-top: 15px;
  }

  .me-info > div,a {
    margin-top: 15px;
    color: #333;
  }

  .skiils {
    width: 70%;
    display: flex;
    flex-direction: column;
  }

  .skills-wrap {
    border: 1px solid #969696;
    border-radius: 10px;
    width: 33%;
    height: auto;
    margin-right: 10px;
  }

  .skills-wrap:first-child {
    margin-left: 10px;
  }

  .skill-header {
    display: flex;
    align-items: center;
    padding: 15px;
    height: 55px;
    font-size: 20px;
    border-radius: 10px 10px 0 0 ;
    color: #fff;
    background-image: linear-gradient(to top, rgb(91,147,194), rgb(54, 106, 155))
  }

  .skill-body {
    width: 100%;
    padding: 5px;
  }

  .skill {
    display: flex;
    width: 100%;
    border: 1px solid #a2a2a2;
    border-radius: 6px;
    box-shadow: 5px 5px 5px 0px #d8d8d8;
    background: #fff;
  }

  .skill > img {
    padding: 4px;
    min-width: 50px;
    height: 50px;
    object-fit: fill;
    /* height: auto; */
    border: 1px solid #333;
    border-radius: 4px;
  }

  .skill > div {
    border: 1px solid rgb(162,162,162);
    background-image: linear-gradient(to top, rgb(214, 214, 214), rgb(236, 236, 236));
    width: 100%;
    padding: 5px 0 0 5px;
  }

  .career-wrap {
    background: #fff;
    padding: 20px;
  }

  .company-desc {
    margin-top: 25px;
    line-height: 30px;
    color: #7a7a7a;
  }